  left: 8vw;
  top: 62vh;
  flex-wrap: wrap;
  .Wallitem{
    display: flex;
    justify-content: center;
    width: 42vw;
    position: relative;
    // padding: 1vw;
    margin: 1vh 0;
    img{
      width: 38vw;
    }
